<template>
    <div class="todo-list">
        <el-input v-model="input" @keyup.enter.native="addTask" placeholder="请输入代办事项，按回车键添加到列表" />
        <el-tabs type="border-card" @tab-click="clickTab">
            <el-tab-pane :key="status" v-for="status in [0,1]" v-bind:label="status==0?'未完成':'已完成'" @lazy="true">
                <el-card>
                    <div v-if="filteredList.length==0" class="placeholder">
                        <span v-if="status==0">没有待办的事呢</span>
                        <span v-if="status==1">还没有已完成的事呢</span>
                    </div>
                    <todo-item :key="index" :msg="todo" :index="index" v-for="(todo,index) in filteredList" v-on:deleteTask="deleteTask(index)" />
                </el-card>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>



<script>
    import ToDoItem from './todo-item.vue'
    export default {
        name: 'todo-list',
        components: {
            'todo-item' : ToDoItem
        },
        created: function () {
            let cookies = this.$cookies.get('todos');
            if(cookies){
                this.todos = JSON.parse(cookies);
            }
        },
        data: () => {
            return {
                input: undefined,
                selectedStatus: 0,
                todos: []
            }
        },
        methods: {
            addTask: function () {
                this.todos.push({
                    text: this.input,
                    status: false
                });
                this.input = '';
            },
            clickTab: function (e) {
                this.selectedStatus = e.index;
            },
            deleteTask: function (index) {
                let task = this.filteredList[index];
                let todoIndex = this.todos.indexOf(task);
                this.todos.splice(todoIndex, 1);
            }
        },
        computed: {
            filteredList: function () {

                this.$cookies.set('todos', JSON.stringify(this.todos));

                return this.todos.filter(item => item.status == this.selectedStatus);
            }
        }
    }
</script>

<style>
    .placeholder{
        opacity: 0.5;
    }
</style>