<template>
    <el-row class="todo-item" type="flex" justify="space-between" v-bind:class="{done: msg.status}" @click.native="toggleStatus">
        <span>
            {{index+1}}. 
            <span @click.stop="editing=true">
                <label v-if="!editing" v-text="msg.text"></label>
                <input v-focus @blur="completeEdit" v-if="editing" v-model="msg.text" @keyup.enter="completeEdit">
            </span>
        </span>
        <span>
            <el-checkbox v-model="msg.status" />    
            <span class="delete" @click.stop="deleteTask">×</span>
        </span>
    </el-row>
</template>



<script>
    export default {
        name: 'todo-item',
        data: function() {
            return {
                editing: false
            };
        },
        props: ['msg', 'index'],
        methods: {
            toggleStatus: function () {
                this.msg.status = !this.msg.status;
            },
            deleteTask : function () {
                this.$emit('deleteTask');
            },
            completeEdit: function () {
                this.editing = false;
                if(!this.msg.text){
                    this.deleteTask();
                }
            }
        },
        directives: {
            focus: {
                inserted: function(el) {
                    el.focus();
                }
            }
        }
    }
</script>

<style>
    input{
        border: none;
        outline: none;
        font-size: 16px;
        float: right;
    }
    .delete{
        color:#ccc;
        padding: 0 3px;
        margin-left: 10px;
    }
    .delete:hover{
        color:#333;
    }
</style>